.advantages {
  padding-top: 32px;
  padding-bottom: 42px;

  .advantages__wrapper {
    width: 280px;
    padding: 0 20px;
  }

  .advantages__list {
    list-style: none;
  }

  h2 {
    font-size: 36px;
    line-height: 60px;
    font-weight: 400;
  }

  p {
    font-family: Arial, Helvetica, sans-serif;
    font-size: 14px;
    line-height: 18px;
  }
}

.advantages__item {
  .green-background {
    width: 60px;
    height: 60px;
    background-color: #68b738;

    display: flex;
    justify-content: center;
    align-items: center;
  }
}